.requirements {
  color: #fff;
  background: url("../images/Layer.png") center no-repeat;
  background-size: cover;
}

.requirements-list{
  justify-content: center;
  &__item {
    margin: 6px;
    background: #3fa0ff;
    color: #fff;
    flex: 1 1 360px;
    @media(min-width: 1140px){
        max-width: 360px;
        min-height: 465px;
    }
    & > h3 {
      margin: 30px 0 10px;
      text-align: center;
      font-size: 23px;
      text-transform: uppercase;
      span {
        text-transform: none;
      }
    }
  }
}

.courses-require {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  margin-left: 35px;
  text-align: left;
  font-weight: 200;
  &__list {
    margin: 15px 0;
    font-size: 15px;
    &:before {
      content: '\2014';
      position: absolute;
      margin-left: -20px;
    }
  }
}
