@import "../base/_custom-variable";



.main-nav {
  width: 100%;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.8);
  padding: 15px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  overflow-y: scroll;
  @media (min-width: 768px) {
    flex-direction: row;
    background-color: transparent;
  }
}

.main-menu {
  display: flex;
  flex-direction: column;
  @media (min-width: 768px) {
    flex-direction: row;
    background-color: transparent;
  }
  li.current-menu-item a {
    text-decoration: underline;
    color: @color-interactive;
  }
  &__item {
    margin: 5vh 10px;
    & > a {
      &:hover {
        color: @color-interactive;
      }
    }
  }
}

.nav-btn {
  padding: 30px 0;
}


.main-nav__logo {
  display: none;
}

.remove-btn {
  display: block;
  align-self: flex-start;
}

@media (min-width: 768px) {
  .remove-btn,
  .change-btn,
  .main-nav-toggle,
  .module {
    display: none;
  }
  .main-nav__logo {
    display: inline-block;
  }
}
