@import "../base/_custom-variable";
.choice-course {
  color: #fff;
  background: url("../images/courses_bg.jpg") center no-repeat;
  background-size: cover;
}


.primary-color {
  color: @color-interactive;
}

.primary-link-color {
  a {
    color: @color-interactive;
    &:hover {
      color: @color-interactive;
    }
  }
}

.secondary-link-color {
  a {
    color: #999;
    &:hover {
      color: @color-interactive;
    }
  }
}

.copyright {
  @extend .secondary-link-color;
}

.contact-list--item {
  @extend .secondary-link-color;
}

.questions-list__item {
  @extend .primary-link-color;
}

.courses-list__item:hover {
  transition: opacity .8s ease-in-out;
  background-color: rgba(63, 160, 255, 0.8);
  color: rgba(0, 0, 0, 0.5);
  .courses-list__item__view {
    display: flex;
  }
}

.scrollBtn:hover {
  background-color: @color-interactive;
  color: #fff;
}

@media (min-width: 992px) {
  .requirements-list {
    justify-content: space-between;
  }
  .courses-list {
    flex-wrap: nowrap;
  }
  .sup-header {
    letter-spacing: 14px;
    font-size: 16px;
  }
}


