.team-list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  &__item {
    position: relative;
    flex: 1 1 25%;
    min-width: 260px;
    max-width: 500px;
    max-height: 500px;
  }
}

.team-shadow-overlay {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(17, 17, 17, 0.9);
  opacity: 0;
  transition: .4s;
  &:hover {
    opacity: 1;

  }
}

.member-team {
  color: #fff;
  font-weight: 400;
  transition: .4s;
  transform: scale(1);
}

.team-img {
  max-height: 318px;
}