.about-list {
  &__item {
    display: flex;
    align-items: center;
    text-align: left;
    margin: 40px 0;
    &_link {
      color: #0f0f0f;
      margin-left: 30px;
    }
    &:before {
      content: "";
      display: inline-block;
      border-top: 1px solid #eee;
      width: 40px;
      margin: 0;
      @media (min-width: 762px) {
        width: 80px;
      }
    }
  }
}

.about-block_courses-icon {
  width: 40px;
  height: 40px;
  margin: 0 0 0 40px;
}