.home {
  width: 100%;
  padding: 120px 0 80px 0;
  background: url(../images/block-info.jpg) center no-repeat;
  background-size: cover;
  text-align: center;
  color: #fff;
  font-weight: 400;
}

.home-intro {
  max-width: 690px;
  margin: 0 auto;
  flex-direction: column;
}

.title-home {
  font-size: 24px;
  letter-spacing: 2px;
  margin: 20px 0;
  font-weight: 500;
  &__child {
    text-transform: none;
  }
}

.home-description {
  font-size: 16px;
  margin: 40px 0;
}