@media (max-width: 767px) {
  .offcanvas {
    right: 0;
    position: absolute;
    height: 100%;
    width: 75%;
    top: 0;
    min-width: 290px;
    position: fixed;
    visibility: hidden;
    z-index: -1;
  }
}
.show-right {
  right: -100%;
  transform: translate3d(100%, 0, 0);
  transition: transform .5s ease;
}
.show-left {
  left: -100%;
  transform: translate3d(-100%, 0, 0);
  transition: transform .5s ease;
}
.show-top {
  top: -100%;
  width: 100%;
  transform: translate3d(0, -100%, 0);
  transition: transform .5s ease;
}
.show-bottom {
  bottom: -100%;
  width: 100%;
  transform: translate3d(0, 100%, 0);
  transition: transform .5s ease;
}
.active {
  z-index: 9999;
  transition: transform .5s ease;
  transform: translate3d(0, 0, 0);
  visibility: visible;
}
.show-right.active {
  right: 0;
}
.show-left.active {
  left: 0;
}
.show-top.active {
  top: 0;
}
.show-bottom.active {
  bottom: 0;
}
