
.courses-list{
  flex-wrap: wrap;
  position: relative;
  width: 100%;
  text-align: center;

  &__item {
    max-width: 370px;
    min-height: 370px;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 5px;
    color: #fff;
    flex: 1 1 370px;  background: #3fa0ff;
    & > h3 {
      margin: 30px 0 10px;
      text-align: center;
      font-size: 23px;
      text-transform: uppercase;
      span {
        text-transform: none;
      }
    }
    &__view {
      position: absolute;
      display: none;
      width: 100%;
      height: 90%;
      justify-content: center;
      align-items: flex-end;
    }
  }
}

.courses-logo {
  img {
    max-width: 120px;
    height: auto;
  }
}