@import "_custom-variable";

@font-primary: 'Source Sans Pro', sans-serif;
@color-interactive: #458bd1;


body {
  position: relative;
  font-family: @font-primary;
  letter-spacing: 1px;
  font-weight: 400;
  color: #999;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
  position: relative;
}

.img-responsive {
  width: 100%;
  height: auto;
}

.spd-logo {
  display: none;
  @media(min-width: 1200px){
    display: block;
  }
}

section {
  padding: 15px 0;
  background-color: #ffffff;
  @media(min-width: 768px){
    padding: 30px 0;
  }
  @media(min-width: 1200px){
    padding: 80px 0;
  }

}

footer {
  background-color: #222;
  font-size: 12px;
  //color: #999;
}

figure {
  margin: 0;
}

a {
  text-decoration: none;
  color: #fff;
  &:hover {
    color: @color-interactive;
    cursor: pointer;
  }
}

* {
  box-sizing: border-box;
}

ul {
  list-style-type: none;
  margin: 0;
}

ol, ul {
  padding-left: 0;
}

h4 {
  font-size: 18px;
}

h2 {
  font-size: 22px;
  @media (min-width: 768px) {
    font-size: 26px;
  }
  @media (min-width: 1200px) {
    font-size: 55px;
  }
}

h3 {
  font-size: 16px;
  @media (min-width: 992px) {
    font-size: 20px;
  }
}
