.steps-list {
  list-style-type: square;
  font-size: 15px;
  color: #999;
  text-align: left;
  margin: 34px 0;
  &__item {
    margin: 26px 16px;
    color: #ac3896;
    span {
      color: #999;
    }
    &:nth-child(2) {
      color: #fed030;
    }
    &:nth-child(3) {
      color: #1674d1;
    }
  }
}
